

.app-menu-bar > .__toolbar {
    display: flex;
    justify-content: space-between;
}

.app-menu-bar > .__toolbar > .__left {
    display: flex;
    align-items: center;
}
.app-spinner{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 128px)
}
.movies__card {
    position: relative;
    max-width: 345px;     
    margin: 15px
}

.movies__card .__media {       
    object-fit: cover;
    height: 200px  
}

.movies__card .__copy {
    height: 1.5em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
}

.movies__card .\--number {
    position: absolute;
    display: block;
    right: 0;
    width: 25px;
    height: 25px;
    background: #0097A7;
    z-index: 1;
    text-align: center;
    border-radius: 0 0 0 3px;
    line-height: 25px;
    color: #fff
}


.movies > main {
    position: relative;    
    width: calc(100% - 30px);
    margin: 64px auto 0 auto;
    padding: 15px;

}

.movies > main > section {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.movies > main > section > h1 {
    width: 100%;
    text-align: center;
    color: #fff;
}

.movies .__search { 
    display: flex;
    flex-direction: column;
    
}


@media screen and (min-width: 964px) {
    .movies .__search { 
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;        
    }

    .movies .__search .__button {
        margin: 16px 0 8px 15px;
        padding: 16px 0;
    }
}



.movie-artwork {
    position: relative;
    flex: 1 0 auto;
    display: flex;    
    align-items: center;
    justify-content: center;
    overflow: hidden;
    width: 100%;
    height: 50vh;
    color:#fff;
    background-size: cover;  
    background-position: 0 25%;   
    box-shadow: inset #000 0 0 70px -6px
}

@media screen and (min-width: 964px) {
    .movie-artwork {
        width: 50vw;
        height: calc(100vh - 64px);
        background-position: 0 50%;
    }
}

.movie-artwork::after {
    content:'';
    position: absolute;
    display: block;
    right: -20%;
    top: -20%;
    left: -20%;
    height: 40%;
    background: #000;
    opacity: 0.8;
    -webkit-filter: blur(40px);
            filter: blur(40px);
    z-index: 0;
}

.movie-artwork .__title {
    font-size: 4em;
    z-index: 1;
    position: relative;
    padding: 1vh 5vw;
    text-align: center;
    text-shadow: rgba(0, 0, 0, 0.9) 0px 0px 10px

    
}

.movie-details {    
    padding: 5vh 5vw;
    color: #fff    
}

.movie-details .__copy {
    margin: 2em 0;
}

.movie {
    margin: 64px 0 0 0;
}

.movie main {
    position: relative;
    display: flex;    
    flex-direction: column;
    flex-wrap: wrap; 
    color: #fff;
       
}

@media screen and (min-width: 964px) {
    .movie main {
        flex-direction: row;
        flex-wrap: nowrap;
    }
}

.login {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.login .__form {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 230px;

}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a{
  text-decoration: none;
  color: inherit;
}

:root{
  background: #212121;
}


